import { Component, ChangeDetectionStrategy, ViewChild } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import {Router} from '@angular/router';
import 'rxjs/add/operator/toPromise';

//import { ModalDirective } from 'ngx-bootstrap/modal/modal.component'; //modales
import { ModalDirective } from 'ngx-bootstrap/modal';

import { RutaService } from '../../services/ruta-service/ruta.service';

import { FormBuilder, FormArray, FormGroup, Validators  } from '@angular/forms';

//import {NgxPermissionsService, NgxRolesService} from 'ngx-permissions';

@Component({
  selector: 'app-clientes',
  templateUrl: 'clientes.component.html',
  changeDetection: ChangeDetectionStrategy.Default,
  styleUrls : ['clientes.css']
})

export class ClientesComponent {

  private data:any;
  public productList:any;

  selectedCliente: any;
  clienteAEliminar: any;
  eliminar_id: any;
  eliminar_user: any;

  public loading = false;
  public editando = false;
  public mostrar = true;

  prueba = 'prueba binding';

  public alerta = false;
  public alerta_boton = false;
  public alerta_tipo: any; //success info warning danger  
  public alerta_msg: any;

    constructor(private http: HttpClient,
      private router: Router,
      private rutaService: RutaService) {


    }

    ngOnInit(): void {

      this.checkLogin();
      
      this.loading = true;
      this.http.get(this.rutaService.getRutaApi()+'usuarios/clientes?token='+localStorage.getItem('luvas_token'))
         .toPromise()
         .then(
           data => { // Success
             console.log(data);
             this.data=data;

             var dia : any;
             var mes : any;
             var anio : any;
             var fecha : any;
             var hora : any;
             var minutos : any;
             var segundos : any;


             for (var i = 0; i < this.data.clientes.length; i++) {
               fecha = new Date(this.data.clientes[i].created_at);
               //this.data.clientes[i].created_at = new Date(this.data.clientes[i].created_at);
               dia = fecha.getDate();
               mes = fecha.getMonth() + 1;
               anio = fecha.getFullYear();

               hora = fecha.getHours();
               minutos = fecha.getMinutes();
               segundos = fecha.getSeconds();

               this.data.clientes[i].fecha = dia+'/'+mes+'/'+anio;
               this.data.clientes[i].hora = hora+':'+minutos+':'+segundos;

               /*console.log('created_at '+this.data.clientes[i].created_at);
               console.log('fecha '+fecha);
               console.log('dia '+dia);
               console.log('mes '+mes);
               console.log('anio '+anio);*/
             }

             setTimeout(()=>{
               this.productList = this.data.clientes;
               this.filteredItems = this.productList;
               //console.log(this.productList);

               this.init();

               this.loading = false;
             },1000);
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
                  this.mostrar = false;
              }
              //sin usuarios
              else if(msg.status == 404){ 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'info';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }
              

           }
         );
    }

    checkLogin(){
      /*Verifiacar si hay un usario logeado y su tipo*/
      var user = localStorage.getItem('luvas_user');
      var user_login = JSON.parse(user);

      if (user_login) {
        if (user_login.tipo_usuario != 1) {
          this.router.navigate(['luvas/home']);
        }
      }else{
        this.router.navigate(['luvas/home']);
      }
    }

    //Alerta cuando se vence el token
    ok_alerta(): void {
      this.router.navigate(['luvas']);
    }

    cerrarAlerta(): void {
      this.alerta = false
    }

    ver(cliente: any): void {
      this.editando = true;
      this.selectedCliente = cliente;
      console.log(this.selectedCliente);

    }

    aEliminar(cliente): void {
      this.clienteAEliminar = cliente;
      //console.log(this.clienteAEliminar);
      //console.log(this.clienteAEliminar.user);
      this.eliminar_id = this.clienteAEliminar.id;
      this.eliminar_user = this.clienteAEliminar.nombre;
      //this.myModal.show();
    }

    eliminar(): void {
      //this.selectedCliente = cliente;
      console.log(this.clienteAEliminar);
      //this.myModal.show();

      this.loading = true;

      var datos= {
        token: localStorage.getItem('luvas_token')
      }

      this.http.delete(this.rutaService.getRutaApi()+'usuarios/'+this.eliminar_id+'?token='+localStorage.getItem('luvas_token'))

         .toPromise()
         .then(
           data => { // Success
              console.log(data);
              this.data = data;

              var aux = this.productList;
              this.productList = [];

              for (var i = 0; i < aux.length; ++i) {
                if (aux[i].id != this.eliminar_id) {
                   this.productList.push(aux[i]);
                }
              }

              this.filteredItems = this.productList;
              this.init();
              
              //console.log(this.productList);
              //alert(this.data.message);

              this.loading = false;

              this.alerta_tipo = 'success';
              this.alerta_msg = this.data.message;
              this.alerta = true;
              setTimeout(()=>{this.alerta = false;},4000);

              
           },
           msg => { // Error
             msg.error = JSON.parse(msg.error);
             console.log(msg);
             console.log(msg.error.error);

             this.loading = false;

             //token invalido/ausente o token expiro
             if(msg.status == 400 || msg.status == 401){ 
                  //alert(msg.error.error);
                  //ir a login

                  this.alerta_tipo = 'warning';
                  this.alerta_msg = msg.error.error;
                  this.alerta_boton = true;
                  this.mostrar = false;
              }
              //no encontrada o confilto
              else if(msg.status == 404 || msg.status == 409){ 
                  //alert(msg.error.error);

                  this.alerta_tipo = 'danger';
                  this.alerta_msg = msg.error.error;
                  this.alerta = true;
                  setTimeout(()=>{this.alerta = false;},4000);
              }

           }
         );
    }

    eliminarModal(): void{
      this.loading = true;
      //this.myModal.hide();
    }

    atras(): void {
      this.editando = false;
      this.selectedCliente = null;
      console.log(this.selectedCliente);
    }

   filteredItems : any;
   pages : number = 4;
   pageSize : number = 5;
   pageNumber : number = 0;
   currentIndex : number = 1;
   items: any;
   pagesIndex : Array<number>;
   pageStart : number = 1;
   inputName : string = '';

   init(){
         this.currentIndex = 1;
         this.pageStart = 1;
         this.pages = 4;

         this.pageNumber = parseInt(""+ (this.filteredItems.length / this.pageSize));
         if(this.filteredItems.length % this.pageSize != 0){
            this.pageNumber ++;
         }
    
         if(this.pageNumber  < this.pages){
               this.pages =  this.pageNumber;
         }
       
         this.refreshItems();
         console.log("this.pageNumber :  "+this.pageNumber);
   }

   FilterByName(){
      this.filteredItems = [];
      if(this.inputName != ""){
            for (var i = 0; i < this.productList.length; ++i) {
              if (this.productList[i].nombre.toUpperCase().indexOf(this.inputName.toUpperCase())>=0) {
                 this.filteredItems.push(this.productList[i]);
              }else if (this.productList[i].email.toUpperCase().indexOf(this.inputName.toUpperCase())>=0) {
                 this.filteredItems.push(this.productList[i]);
              }else if (this.productList[i].telefono.indexOf(this.inputName)>=0) {
                 this.filteredItems.push(this.productList[i]);
              }
              else if (this.productList[i].fecha.toUpperCase().indexOf(this.inputName.toUpperCase())>=0) {
                 this.filteredItems.push(this.productList[i]);
              }
            }

            // this.productList.forEach(element => {
            //     if(element.nombre.toUpperCase().indexOf(this.inputName.toUpperCase())>=0){
            //       this.filteredItems.push(element);
            //    }
            // });
      }else{
         this.filteredItems = this.productList;
      }
      console.log(this.filteredItems);
      this.init();
   }
   fillArray(): any{
      var obj = new Array();
      for(var index = this.pageStart; index< this.pageStart + this.pages; index ++) {
                  obj.push(index);
      }
      return obj;
   }
   refreshItems(){
       this.items = this.filteredItems.slice((this.currentIndex - 1)*this.pageSize, (this.currentIndex) * this.pageSize);
       this.pagesIndex =  this.fillArray();
   }
   prevPage(){
      if(this.currentIndex>1){
         this.currentIndex --;
      } 
      if(this.currentIndex < this.pageStart){
         this.pageStart = this.currentIndex;
      }
      this.refreshItems();
   }
   nextPage(){
      if(this.currentIndex < this.pageNumber){
            this.currentIndex ++;
      }
      if(this.currentIndex >= (this.pageStart + this.pages)){
         this.pageStart = this.currentIndex - this.pages + 1;
      }
 
      this.refreshItems();
   }
    setPage(index : number){
         this.currentIndex = index;
         this.refreshItems();
    }
  
}



